import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Sudoku from './views/Sudoku.vue'
import Loop from './views/Loop.vue'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/sudoku',
			name: 'sudoku',
			component: Sudoku
		},
		{
			path: '/loop',
			name: 'Loop',
			component: Loop
		}
	]
})
